<script>
export default {
  name: "LichHopNgay",
  data() {
    return {
      // attributes: [
      //   {
      //     key: "today",
      //     highlight: {
      //       backgroundColor: "#ff8080",
      //     },
      //     dates: new Date(),
      //   },
      // ],
      // dataNotification: [],
      activeId: null,
      filterDate: null,
      currentId: "khan-cap",
      currentNotiId: 1,
    };
  },
  methods: {
    addZero(num) {
      if (num < 10) {
        return `0${num}`;
      } else {
        return num;
      }
    },
    changeColor(_class) {
      setTimeout(() => {
        let listClass = document.getElementsByClassName(_class);
        listClass.forEach((el) => {
          el.style.backgroundColor = `#${_class}`;
        });
      });
    },
    clickMeetingItem(e) {
      this.activeId = e.id;
      this.$store.dispatch("schedule/getDetail", e.id);
    },
    dayclick(day) {
      this.filterDate = day.dateTime;
    },
    pageChange(page) {
      this.filterDate = null;
      this.$store.dispatch("schedule/getSchedule", {
        month: page.month,
        year: page.year,
      });
    },
    handleClickAttachment(item) {
      if (item.url) window.open(item.url, "_blank");
    },
  },
  computed: {
    currentMeeting() {
      return this.$store.getters["schedule/getDetail"];
    },
    events() {
      if (this.filterDate) {
        return this.dataNotification.filter((data) => {
          return data.filterTime === this.filterDate;
        });
      } else {
        return this.dataNotification;
      }
    },
    dataNotification() {
      let schedule = this.$store.getters["schedule/getSchedules"];
      if (schedule) {
        if (schedule.dataNotification && schedule.dataNotification.length > 0) {
          this.$store.dispatch(
            "schedule/getDetail",
            schedule.dataNotification[0].id
          );
        }
        return schedule.dataNotification;
      }
      return null;
    },
    attributes() {
      let _att = [
        {
          key: "today",
          highlight: {
            backgroundColor: "#ff8080",
          },
          dates: new Date(),
        },
      ];
      if (this.$store.getters["schedule/getSchedules"]) {
        let highlightColor = this.$store.getters["schedule/getSchedules"]
          .highlight;
        for (const key in highlightColor) {
          if (highlightColor[key]) {
            const element = highlightColor[key];
            _att.push(element);
            this.changeColor(key);
          }
        }
      }
      return _att;
    },
  },
};
</script>

