<template>
  <div class="wapper-calender">
    <FullCalendar
      ref="fullCalendar"
      :options="calendarOptions"
      :events="{ events }"
    />
  </div>
</template>

<script>
import FullCalendar from "@fullcalendar/vue";
import dayGridPlugin from "@fullcalendar/daygrid";
import interactionPlugin from "@fullcalendar/interaction";
export default {
  name: "LichHopThang",
  components: {
    FullCalendar,
  },
  data() {
    return {};
  },
  computed: {
    calendarOptions() {
      let options = {
        plugins: [dayGridPlugin, interactionPlugin],
        height: "100%",
        initialView: "dayGridMonth",
        locale: "vi",
        firstDay: 1,
        titleFormat: { year: "numeric", month: "short" },
        headerToolbar: { right: "prev,next" },
        eventOrder: ["title"],
        events: [],
        eventTimeFormat: { hour: "2-digit", minute: "2-digit" },
        eventDidMount: this.eventRender,
        viewDidMount: this.viewMount,
        customButtons: {
          prev: {
            // this overrides the prev button
            text: "PREV",
            click: () => {
              let calendarApi = this.$refs.fullCalendar.getApi();
              calendarApi.prev();
              let currentDate = calendarApi.getDate();
              this.fetchData(currentDate);
            },
          },
          next: {
            // this overrides the next button
            text: "PREV",
            click: () => {
              let calendarApi = this.$refs.fullCalendar.getApi();
              calendarApi.next();
              let currentDate = calendarApi.getDate();
              this.fetchData(currentDate);
            },
          },
        },
      };
      if (this.$store.getters["schedule/getSchedules"]) {
        options.events = this.$store.getters[
          "schedule/getSchedules"
        ].dataNotification;
        return options;
      }
      return options;
    },
  },
  methods: {
    eventRender(eventInfo) {
      eventInfo.el.firstChild.style.borderColor = `#${eventInfo.event._def.extendedProps.status_color}`;
    },
    fetchData(currentDate) {
      this.$store.dispatch("schedule/getSchedule", {
        month: currentDate.getMonth() + 1,
        year: currentDate.getFullYear(),
      });
    },
  },
  mounted() {
    let currentDate = new Date();
    this.fetchData(currentDate);
  },
};
</script>

