<template>
  <div style="position: relative">
    <p class="title-lichhop">Lịch Họp</p>
    <b-row>
      <b-col>
        <b-tabs>
          <b-tab
            lazy
            :key="1"
            title="Ngày"
            :active="activeTab === `#${LICH_HOP_TAB.LICH_HOP_NGAY}`"
            @click="changeCurrentTab(LICH_HOP_TAB.LICH_HOP_NGAY)"
          >
            <div class="">
              <LichHopNgay />
            </div>
          </b-tab>
          <b-tab
            lazy
            :key="3"
            title="Tháng"
            :active="activeTab === `#${LICH_HOP_TAB.LICH_HOP_NAM}`"
            @click="changeCurrentTab(LICH_HOP_TAB.LICH_HOP_NAM)"
          >
            <div class="">
              <LichHopThang />
            </div>
          </b-tab>
        </b-tabs>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import { ROUTE_NAME, LICH_HOP_TAB } from "@/constants/constants";
import LichHopNgay from "@/components/pages/lich-hop/LichHopNgay";
import LichHopThang from "@/components/pages/lich-hop/LichHopThang";

export default {
  name: "LichHop",
  data() {
    return {
      activeTab: "",
      ROUTE_NAME,
      LICH_HOP_TAB,
    };
  },
  components: {
    LichHopNgay,
    LichHopThang,
  },
  created() {
    this.activeTab = location.hash;
  },
  mounted() {
    let currentDate = new Date();
    this.$store.dispatch("schedule/getSchedule", {
      month: currentDate.getMonth() + 1,
      year: currentDate.getFullYear(),
    });
  },
  methods: {
    changeCurrentTab(current) {
      this.$router
        .push({
          name: ROUTE_NAME.CHUONG_TRINH_HOP.NAME,
          hash: `#${current}`,
        })
        .catch(() => {});
    },
  },
};
</script>

